import React from 'react'
import ReactSpeedometer from "react-d3-speedometer"
import { Link } from 'react-router-dom'

const Speed4 = () => {

  function randomRandom(){

   let randFunc = Math.random() 
   
    let lottery = randFunc <0.5 ? Math.floor(Math.random() * -100) : Math.floor(Math.random() * 100)
    return lottery}

   

  return (
    <>
<Link to="/" style={{marginLeft: "45%"}}>Strona główna</Link>
   
  

<ReactSpeedometer
            width={400}
            height={400}
            needleHeightRatio={0.7}
            segments={12}
            maxValue={100}
            minValue={-100}
            maxSegmentLabels={11}
            value={randomRandom()}
            currentValueText="Różnica napięć ${value}"
            customSegmentStops={[-100, -80, -60, -40, -20, 0, 20, 40, 60, 80, 100]}
            
            ringWidth={47}
            needleTransitionDuration={3333}
            needleTransition="easeElastic"
            needleColor={'steelblue'}
            textColor={'black'}

            
          />
</>
  )
}

export default Speed4