import React, { useEffect } from 'react'
import {Link} from 'react-router-dom'
import ReactSpeedometer from "react-d3-speedometer"
import{useState} from "react"
import { Chart } from "react-google-charts";
import Speedometer, {
  Background,
  Arc,
  Needle,
  Progress,
  Marks,
  Indicator,
  DangerPath
} from 'react-speedometer';




const FazΔΦ12 = () => {

  const [number, setNumber] = useState(0)

  function randomRandom(){
    setInterval(()=>{

    let randFunc = Math.random() 
    
     let lottery = randFunc <0.5 ? Math.floor(Math.random() * -180) : Math.floor(Math.random() * 180); setNumber(lottery)
     return lottery}, 1000) }

     function randomRandom1(){

      let randFunc = Math.random() 
      
       let lottery = randFunc <0.5 ? Math.floor(Math.random() * -100) : Math.floor(Math.random() * 100)
       return lottery}

    

   useEffect(()=>{

    function dodatnie(){
      for (let i=0;  i<=180; i++){delay(i)}

    function delay(i) {setTimeout(() => {
    console.log(i)
    setNumber(i)
    }, 50 * i );}}
 

 function ujemne(){
  for (let i=0;  i>=-180; i--){delay(i)}

 function delay(i) {setTimeout(() => {
  console.log(i)
 setNumber(i)
 }, 50 * -i );}}

setTimeout(ujemne, 9000)
dodatnie()

}, [] )

                   
      let minValue = -38
     let maxValue = 38  
     let currentValue = randomRandom1()

 


            
  return (

    
    <div className="charts">
      <Link to="/">Strona główna</Link>
      <h3>Różnca faz ΔΦ12</h3>
      
    
    <ReactSpeedometer
            width={400}
            height={300}
            needleHeightRatio={0.7}
            segments={12}
            maxValue={180}
            minValue={-180}
            maxSegmentLabels={12}
            value={number}
            currentValueText="Różnica faz: ${value}"
            customSegmentStops={[-180, -150, -120, -90, -60, `${minValue}`,  0, `${maxValue}`, 60, 90, 120, 150, 180]}
            segmentColors={['red', 'red', 'red','red', 'red', 'green', 'green','red', 'red', 'red','red', 'red' ]}
            
            ringWidth={47}
            needleTransitionDuration={0}
            needleTransition="easeElastic"
            needleColor={'steelblue'}
            textColor={'black'}

            
          />

<Speedometer
    value={number}
    min={-180}
    max={180}
    fontFamily='squada-one'
    angle={360}
  >
    <Background />
    <Arc/>
    <Needle/>
    <Marks/>
    <DangerPath/>
    <Indicator/>
  </Speedometer>

          
<Chart
          chartType="Gauge"
          forceIFrame="true"
          width="100%"
          height="400px"
          easing="constant"
          data={[
            ["Label", "Value"],
            ["ΔΦ12", number]
            
          ]}
          options={{
            animation:{
              startup: "true",
              easing: "linear"
            },
            width: 400,
            height: 300,
            min: -180,
            max: 180,
            greenFrom: minValue,
            greenTo: maxValue,
            majorTicks:[-180, -150, -120, -90, -60, -30, 0, 30, 60, 90, 120, 150, 180],
            minorTicks: 10,
          }}
        />

  
  </div>
  )



  }


export default FazΔΦ12