import React from 'react'
import {Link} from 'react-router-dom'
import Speedometer, {
  Background,
  Arc,
  Needle,
  Progress,
  Marks,
  Indicator,
  DangerPath
} from 'react-speedometer';



const często12 = () => {

    function randomRandom(){

        let randFunc = Math.random() 
        
         let lottery = randFunc <0.5 ? Math.floor(Math.random() * -500) : Math.floor(Math.random() * 500)
         return lottery}

         
      let minValue = -200
      let maxValue = 200
      let currentValue = randomRandom()

      

     

  return (
    <div className="charts">
        <Link to="/">Strona główna</Link>
        <h3>Różnica częstotliwości Δf12 </h3>

        <Speedometer
        value={randomRandom()}
        min={-180}
        max={180}
       fontFamily='squada-one'
       angle={360}
                 >
    <Background />
    <Arc/>
    <Needle/>
    <Marks/>
    <DangerPath/>
    <Indicator/>
  </Speedometer>

        

            </div>
            )
          }

export default często12