import React, { useEffect, useState } from 'react'
import {Link} from 'react-router-dom'
import Speedometer, {
  Background,
  Arc,
  Needle,
  Marks,
  Indicator,
  
} from 'react-speedometer';




const RóżnicaFaz = () => {

  const [number1, setNumber1] = useState(0)

     function randomRandom1(){

      let randFunc = Math.random() 
      
       let lottery = randFunc <0.5 ? Math.floor(Math.random() * -100) : Math.floor(Math.random() * 100)
       return lottery}

    
       useEffect(()=>{

        function dodatnie(){
          for (let i=0;  i<=180; i++){delay(i)}
    
        function delay(i) {setTimeout(() => {
        console.log(i)
        setNumber1(i)
        }, 50 * i );}}
     
    
     function ujemne(){
      for (let i=0;  i>=-180; i--){delay(i)}
    
     function delay(i) {setTimeout(() => {
      console.log(i)
     setNumber1(i)
     }, 50 * -i );}}
    
    setTimeout(ujemne, 9000)
    dodatnie()
    
    }, [] )

                   
      let minValue = -38
     let maxValue = 38  
     let currentValue = randomRandom1()


            
  return (

    
    <div className="charts">
      <Link to="/">Strona główna</Link>
      <h3>Różnica faz ΔΦ12</h3>
        <div className="rectangle" style={{backgroundColor: number1 < minValue || number1 > maxValue ?  "red" : "green"}} >
        <h3>{number1 < minValue || number1 > maxValue ? "brak synchronizmu" : "synchronizm"}</h3>
      </div>

<Speedometer
    value={number1}
    min={-180}
    max={180}
    fontFamily='squada-one'
    angle={360}
   
  >
    <Background />
    <Arc/>
    <Needle/>
    <Marks step={15}/>
    <Indicator/>
  </Speedometer>

  <h3 style={{fontWeight: "bold"}}>Minimalna różnica faz: {minValue}</h3>
      <h3 style={{fontWeight: "bold"}}>Maksymalna różnica faz: {maxValue}</h3>
      

          


  
  </div>
  )



  }


export default RóżnicaFaz