import React, { useEffect } from 'react'
import {Link} from 'react-router-dom'
import{useState} from "react"

import Speedometer, {
  Background,
  Arc,
  Needle,
  Progress,
  Marks,
  Indicator,
  DangerPath
} from 'react-speedometer';




const RóżnicaNapięć1 = () => {

  const [number, setNumber] = useState(0)

  

     function randomRandom(){

      let randFunc = Math.random() 
      
       let lottery = randFunc <0.5 ? Math.floor(Math.random() * -100) : Math.floor(Math.random() * 100)
       return lottery}

    

                   
      let minValue = -38
     let maxValue = 38  
     let currentValue = randomRandom()


            
  return (

    
    <div className="charts">
      <Link to="/">Strona główna</Link>
      <h3>Różnica napięć ΔΦ12</h3>   

      <div className="rectangle" style={{backgroundColor: currentValue < minValue || currentValue > maxValue ?  "red" : "green"}} >
        <h3>{currentValue < minValue || currentValue > maxValue ? "brak synchronizmu" : "synchronizm"}</h3>
      </div>

<Speedometer
    value={currentValue}
    min={-100}
    max={100}
    fontFamily='squada-one'
    angle={220}
    marks={30}
   
  >
    <Background />
    <Arc/>
    <Needle/>
    <Marks />
    <Indicator/>
  </Speedometer>

  <h3 style={{fontWeight: "bold"}}>Minimalna różnica napięć: {minValue}</h3>
      <h3 style={{fontWeight: "bold"}}>Maksymalna różnica napięć: {maxValue}</h3>

          


  
  </div>
  )



  }


export default RóżnicaNapięć1