import React from 'react'
import logo from "./mikronika-logo.png" 

const Header = () => {
  return (
   <div className='header-main'>
    <img src={logo} width="171" height="64" alt="header-logo"/>
    <h2 className="header-logo">WIZUALIZACJA PRACY MODUŁU KONTROLI SYNCHRONIZMU</h2>
   </div>
  )
}

export default Header
