import React from 'react'
import { Link } from 'react-router-dom'
import {useState, useEffect} from "react"
import ReactSpeedometer from "react-d3-speedometer"
import { Chart } from "react-google-charts";



const RóżnicaNapięćΔU12 = () => {

  const [needle, setNeedle] = useState(0)

const handleClick2 = () => {
  // 👇️ use document.getElementById()
  const arrow = document.getElementById('arrow');
 setNeedle(+30)
  arrow.style = `transform: rotate(${needle}deg)`
  console.log(arrow);};

  function randomRandom(){

    let randFunc = Math.random() 
    
     let lottery = randFunc <0.5 ? Math.floor(Math.random() * -100) : Math.floor(Math.random() * 100)
     return lottery}

      let minValue = -38
     let maxValue = 38
     let currentValue = randomRandom()

  return (
    <div className="charts">
    <Link to="/">Strona główna</Link>
    <h3>Różnica napięć ΔU12</h3>

    <ReactSpeedometer
        width={400}
        height={300}
        needleHeightRatio={0.7}
        segments={10}
        maxValue={100}
        minValue={-100}
        maxSegmentLabels={11}
        segmentColors={['red', 'red', 'red', 'green', 'green', 'green', 'green', 'red', 'red', 'red']}
        customSegmentStops={[-100, -80, -60,  `${minValue}`, -20, 0, 20, `${maxValue}`, 60, 80, 100]}
        value={randomRandom()}
        currentValueText="Różnica napięć ${value}"
        ringWidth={47}
        needleTransitionDuration={0}
        needleTransition="easeElastic"
        needleColor={'steelblue'}
        textColor={'black'}

        
      />

       <Chart
          chartType="Gauge"
          width="100%"
          height="400px"
          data={[
            ["Label", "Value"],
            ["ΔU12", currentValue]
            
          ]}
          options={{
            animation:{
              startup: "true",
              easing: "linear"
            },
            width: 400,
            height: 300,
            min: -100,
            max: 100,
            greenFrom: minValue,
            greenTo: maxValue,
            majorTicks:[-100, -80, -60, -40, -20, 0, 20, 40, 60, 80, 100],
            minorTicks: 10,
          }}
        />

<svg width="400" height="400">
        <circle id="outerCircle"cx="200" cy="200" r="150" fill='none' stroke="black"> </circle>
        <circle id="gaugeCircle"cx="200" cy="200" r="20" fill='steelblue' stroke="black"> </circle>
        <line id="arrow" x1="200" y1="200" x2="200" xy="200" stroke="green"></line>
       
    </svg>


</div>
  )
}

export default RóżnicaNapięćΔU12