import React from 'react'
import {Link} from 'react-router-dom'
import Speedometer, {
    Background,
    Arc,
    Needle,
    Progress,
    Marks,
    Indicator,
    DangerPath
  } from 'react-speedometer';



const RóżnicaCzęst = () => {

    function randomRandom(){

        let randFunc = Math.random() 
        
         let lottery = randFunc <0.5 ? Math.floor(Math.random() * -500) : Math.floor(Math.random() * 500)
         return lottery}

         
      let minValue = -200
      let maxValue = 200
      let currentValue = randomRandom()

      

     

  return (
    <div className="charts">
        <Link to="/">Strona główna</Link>
        <h3>Różnica częstotliwości Δf12 </h3>


<div className="rectangle" style={{backgroundColor: currentValue < minValue || currentValue > maxValue ?  "red" : "green"}} >
        <h3>{currentValue < minValue || currentValue > maxValue ? "brak synchronizmu" : "synchronizm"}</h3>
      </div>
        <Speedometer
    value={currentValue}
    min={-500}
    max={500}
    fontFamily='squada-one'
    angle={220}
    marks={200}
   
  >
    <Background/>
    <Arc/>
    <Needle/>
    <Marks step={50}/>
    
    <Indicator/>
  </Speedometer>

  <h3 style={{fontWeight: "bold"}}>Minimalna różnica częstotliwości: {minValue}</h3>
      <h3 style={{fontWeight: "bold"}}>Maksymalna różnica częstotliwości: {maxValue}</h3>
      


        

            </div>
            )
          }

export default RóżnicaCzęst