import React from 'react'
import { Link } from 'react-router-dom'
import arrow from './arrow1.svg'
import {useState, useEffect} from "react"
import RightSide from './RightSide'


let degrees1 = 0
let degrees2 = 0
let degrees3 = 0

const Charts = () => {

 
 
  const [degrees, setDegrees] = useState(0)

 /* useEffect(() => {
    const arrow = document.getElementById('arrow2')
    arrow.style = `transform: rotate(${degrees}deg)`
   }, [degrees]);*/

  const handleClick0 = ()=>{
    setDegrees(prev=>  prev + 100)
    }

 

  const handleClick = () => {
    // 👇️ use document.getElementById()
    const arrow = document.getElementById('arrow');
    degrees1+=30
    arrow.style = `transform: rotate(${degrees1}deg)`
    console.log(arrow);
    setDegrees(prev=> prev+30)
    
      console.log(degrees)};

     const handleClick1 = () => {
      // 👇️ use document.getElementById()
      const arrow = document.getElementById('arrow');
      degrees1-=30
      arrow.style = `transform: rotate(${degrees1}deg)`
      console.log(arrow);
      setDegrees(prev=> prev-30)
      console.log(degrees)}

      const handleClick2 = () => {
        // 👇️ use document.getElementById()
        const arrow = document.getElementById('arrow1');
        degrees2+=20
        arrow.style = `transform: rotate(${degrees2}deg)`
        console.log(arrow);};
    
         const handleClick3 = () => {
          // 👇️ use document.getElementById()
          const arrow = document.getElementById('arrow1');
          degrees2-=20
          arrow.style = `transform: rotate(${degrees2}deg)`
          console.log(arrow);
          }

          const handleClick4 = () => {
            // 👇️ use document.getElementById()
            const arrow = document.getElementById('arrow2');
            degrees3+=20
            arrow.style = `transform: rotate(${degrees3}deg)`
            console.log(arrow);};
        
             const handleClick5 = () => {
              // 👇️ use document.getElementById()
              const arrow = document.getElementById('arrow2');
              degrees3-=20
              arrow.style = `transform: rotate(${degrees3}deg)`
              console.log(arrow);
              }

              const randomDegree = ()=>{
                let rand = Math.floor(Math.random() * 1000)
                const arrow = document.getElementById('arrow2')
                arrow.style = `transform: rotate(${rand}deg)`
              }


              useEffect(()=>{


        
                function zmiana(){
               for (let i=0;  i<=360; i++){delay(i)}
              
              function delay(i) {setTimeout(() => {
                const arrow = document.getElementById('arrow')
               console.log(i)
               setDegrees(i)
               arrow.style = `transform: rotate(${i}deg)`
              }, 50 * i );}}
             zmiana()
            
            
            
            }, [] )
            


 return (
   

<>
<div className="everything">
    <div className="allCharts">

      <div className="charts">
        <Link to="/">Strona główna</Link>
        <h3>Różnica faz</h3>

        <div className="top">
        
          <p className="astrics">*</p>
          <p>-30</p>
          <p>0</p>
          <p>30</p>
          <p className="astrics">*</p>
        </div>
      
        <div className="outerCircle">
            <div className="right">
            <p>-60</p>
            <p>-90</p>
            <p>-120</p>

          </div>

          <div className="circle1">
          
          <img src={arrow} alt="arrow" id="arrow"/></div>

          <div className="left">
            <p>60</p>
            <p>90</p>
            <p>120</p>
          
          </div>  
          
        </div> 

      <div className="bottom">
        <p className="astrics">*</p>
        <p>-150</p>
        <p>+/-180</p>
        <p>150</p>
        <p className="astrics">*</p>

      </div>
    <div className="buttons">
    <button id="button1" className = "button" onClick={handleClick1}>Przesuń w lewo</button>  
    <button id="button2"className="button" onClick={handleClick}>Przesuń w prawo</button>
    </div>
    
    
    </div>

    <div className="charts">
        <Link to="/">Strona główna</Link>
        <h3>Różnica napięć</h3>

        <div className="top">
          <p className="astrics">*</p>
          <p>40</p>
          <p>20</p>
          <p>0</p>
          <p>20</p>
          <p>40</p>
          <p className="astrics">*</p>
        </div>
      
        <div className="outerCircle">
            <div className="right">
            <p>60</p>
            <p>80</p>
            <p>100</p>
            <p className="astrics">*</p>
            <p className="astrics">*</p>

          </div>

          <div className="circle1">

            <img src={arrow} alt="arrow" id="arrow1"/></div>

            <div className="left">
            <p>60</p>
            <p>80</p>
            <p>100 kv</p>
            <p className="astrics">*</p>
            <p className="astrics">*</p>
          
          </div>  

          
          
        </div> 
        <div className="bottom"></div>
 
    <div className="buttons">
    <button id="button3" className = "button" onClick={handleClick3}>Przesuń w lewo</button>  
    <button id="button4" className="button" onClick={handleClick2}>Przesuń w prawo</button>
    </div>
    
    
    </div>

    <div className="charts">
        <Link to="/">Strona główna</Link>
        <h3>Różnica częstotliwości</h3>

        <div className="top">
          <p className="astrics">*</p>
          <p>200</p>
          <p>100</p>
          <p>0</p>
          <p>100</p>
          <p>200</p>
          <p className="astrics">*</p>
        </div>
      
        <div className="outerCircle">
            <div className="right">
            <p>300</p>
            <p>400</p>
            <p>500</p>
            <p className="astrics">*</p>
            <p className="astrics">*</p>

          </div>

          <div className="circle1">

          <img src={arrow} alt="arrow" id="arrow2"/></div>
              
              <div className="left">
            <p>300</p>
            <p>400</p>
            <p>500 mHz</p>
            <p className="astrics">*</p>
            <p className="astrics">*</p>
          
          </div>  

          
          
        </div> 
        <div className="bottom"></div>


    <div className="buttons">
    <button id="button5"className = "button" onClick={handleClick5}>Przesuń w lewo</button>  
    <button id="button6"className="button" onClick={handleClick4}>Przesuń w prawo</button>
    </div>
    
    
    </div>

    </div>
    <RightSide/>
    </div>
    </>
  )
}

export default Charts